<template>
  <el-dialog
    width="670px"
    :title="(isCreate ? '新增':'编辑')+'库位'"
    :visible.sync="isVisible"
    @close="cancel"
  >
    <div class="org-form">
      <el-form :inline="true" ref="orgForm" :model="orgForm" :rules="orgFormRules" :disabled="isFormDisabled" label-width="100px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="库位名称" prop="storageName">
              <el-input v-model="orgForm.storageName" maxlength="30"/>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="类型" prop="storageType">
              <el-select clearable placeholder="请选择库位类型" v-model="orgForm.storageType" >
                <el-option
                  v-for="(item, index) in storageTypeList"
                  :key="index"
                  :label="item.typeName"
                  :value="item.typeId"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="长度(米)" prop="storageLength">
              <el-input-number :min="0" :max="999" :step="0.01" :precision="2" v-model="storageLength" @change="getCapacity" controls-position="right" size="mini"/>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="宽度(米)" prop="storageWidth">
              <el-input-number :min="0" :max="999" :step="0.01" :precision="2" v-model="storageWidth" @change="getCapacity" controls-position="right" size="mini"/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="容量(米²)" prop="storageCapacity">
              <el-input-number :min="0" :max="999999" :step="0.01" :precision="2" v-model="storageCapacity" controls-position="right" size="mini"/>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="状态" prop="storageStatus">
              <el-radio-group v-model="orgForm.storageStatus">
                <el-radio :label="1">启用</el-radio>
                <el-radio :label="2">禁用</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="描述" prop="storageDesc" style="width:100%">
              <el-input type="textarea"
                        style="width:495px"
                        v-model="orgForm.storageDesc"
                        :autosize="{maxRows:3}"
                        maxlength="200"
                        resize="none"
                        placeholder="填写库位描述，最长200字符"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24" class="flex-right">
            <el-button type="primary" @click="save">保存</el-button>
            <el-button @click="cancel">取消</el-button>
          </el-col>
        </el-row>
      </el-form>
    </div>
  </el-dialog>
</template>
<script>
import Validator from '@/utils/extendValidate/extendValidate'
// api
import { getOrgList } from '@/api/common'
import { updateShelves, deleteShelves } from '@/api/warehouse'

const InitForm = {
  pId: '',
  id: '',
  name: '',
  size: '',
  length: '',
  width: '',
  desc: '',
  storageType: '',
  storageStatus: 1,
  storageDesc: '',
  storageUuid: '',
  storageName: '',
  storagePid: ''

}

export default {
  name: 'ShelvesCreate',
  data () {
    return {
      storageLength: '',
      storageWidth: '',
      storageCapacity: '',
      showDetailMode: true,
      isVisible: false,
      isFormDisabled: false,
      organization: [],
      orgForm: {},
      storageTypeList: [
        {typeId: '1', typeName: '普通库位'},
        {typeId: '2', typeName: '收货区'},
        {typeId: '3', typeName: '备货区'},
        {typeId: '4', typeName: '质检区'},
        {typeId: '5', typeName: '待收货区'},
        {typeId: '6', typeName: '维修区'},
        {typeId: '7', typeName: '待发货区'},
        {typeId: '8', typeName: '发货区'}
      ],
      orgFormRules: {
        storageName: [
          {required: true, message: '请填写库位名称', trigger: 'blur'},
          {max: 30, message: '最大长度为30字符', trigger: 'blur'}
        ],
        storageType: [
          {required: true, message: '请选择库位类型', trigger: 'blur'},
          {max: 30, message: '最大长度为30字符', trigger: 'blur'}
        ],
        storageId: [
          {required: true, message: '请输入库位编号', trigger: 'blur'},
          {max: 30, message: '最大长度为30字符', trigger: 'blur'}
        ],
        storageStatus: [
          {required: true, message: '请选择库位状态', trigger: 'blur'}]
      }
    }
  },
  props: {
    show: {
      type: Boolean,
      required: true,
      default: false
    },
    nodeData: {
      type: Object,
      required: true
    },
    isCreate: {
      type: Boolean,
      required: true
    },
    selectedId: {
      type: String,
      required: true
    }
  },
  computed: {

  },
  watch: {
    show (isShow) {
      if (isShow) {
      this.orgForm = Object.assign({}, InitForm)

        // 新增
        if (this.isCreate) {
          this.setToCreateState()
          console.log(this.orgForm)
          // 编辑
        } else {
          this.setToEditState()
          console.log(this.orgForm)
        }
      }

      // 设置窗口显示状态
      this.isVisible = isShow
    }
  },
  methods: {
    setToCreateState () {
      const parentNode = this.nodeData
      if (parentNode) {
        this.orgForm.storagePid = this.nodeData.id
      } else {
        this.$message({
          message: '未找到父节点,无法在此情况下创建库位.',
          type: 'error'
        })
        this.isFormDisabled = true
      }
    },
    setToEditState () {
      this.orgForm = Object.assign(this.orgForm, this.nodeData)
      let originData = Object.assign({}, this.nodeData)
      this.storageLength = originData.length
      this.storageWidth = originData.width
      this.storageCapacity = originData.size
      this.orgForm.storageDesc = originData.desc
      this.orgForm.storageName = originData.name
      this.orgForm.storageUuid = originData.id
      this.orgForm.storagePid = originData.pId
      this.orgForm.storageStatus = Number(originData.storageStatus)
    },
    getCapacity () {
      this.$nextTick(() => {
        if (this.storageLength && this.storageWidth) {
          this.storageCapacity = Number(this.storageLength) * Number(this.storageWidth).toString()
        }
      })
    },
    save () {
      this.$refs.orgForm.validate(isValid => {
        if (isValid) {
          const submitText = this.isCreate ? '新增' : '修改'
          let params = {
            storageWarehouseUuid: this.selectedId,
            isCreate: this.isCreate,
            ...this.orgForm,
            storageLength: this.storageLength,
            storageWidth: this.storageWidth,
            storageCapacity: this.storageCapacity
          }
          updateShelves(params).then(res => {
            if (!res.error) {
              this.$message({
                message: `${submitText}成功!`,
                type: 'success'
              })
              // 重新读取tree
              this.$emit('updateTree')
              this.cancel()
              // 库位详情更新
              params.length = params.storageLength
              params.width = params.storageWidth
              params.size = params.storageCapacity
              params.desc = params.storageDesc
              params.name = params.storageName
              params.id = params.storageUuid
              params.pId = params.storagePid
              this.$emit('handleNodeClick', params)
            }
          })
        }
      })
    },
    cancel () {
      this.isVisible = false
      this.$emit('update:show', false)
      this.orgForm = Object.assign({}, InitForm)
      this.storageLength = ''
      this.storageWidth = ''
      this.storageCapacity = ''
      this.$refs.orgForm.resetFields()
    }
  }
}
</script>
<style lang="scss" scoped>
  .org-content {
    display: flex;
    flex-direction: column;
    align-items: center;

    .org-form{
      width:80%;
      max-width:1000px;
      min-width:600px;
      margin:10px;
      padding:10px;
      border:1px solid $base-light-color;
      border-radius:8px;
    }
  }
</style>



